body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
  }
.jumbotron {
    background-image: url("https://static01.nyt.com/images/2019/12/06/books/06critics-list1/06critics-list1-superJumbo.jpg?quality=90&auto=webp");
    opacity: 0.75;
    color: white;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 450px !important;
    width: 100%;
    padding-top: 10;
    text-align: center;
    font-family: 'Open Sans', sans-serif;
    border-radius: 0;
    /* margin-top: -6vh; */
  }
  
  .jumboTitle,
  .jumboText {
    text-shadow: 2px 3px 4px #000000;
  }
* {
    font-family: 'Open Sans', sans-serif;
    color: #8a4384;
    }
  
  .card {
    width: 100%;
    padding-bottom: 2vh;
    border: 2px solid #8a4384;
  }
  
  .card-image {
    padding-left: 5vw;
    padding-top: 2vh;
    padding-bottom: 2vh;
  }
  
  .card-title {
    font-family: 'Open Sans', sans-serif;
    color: #8a4384;
  }
  
  .btnRow {
    width: 100%;
    display: flex;
    justify-content: flex-end;
  }
  
  .deleteBook {
    margin-left: 10px;
    background-color: #608773;
    color: white;
  }
  
  .backToSearchBtn {
    margin-left: 5px;
    background-color: #C47E9E !important;
    color: white
  }
  
  .backToSearchBtn:hover {
    margin-left: 5px;
    background-color: #C47E9E !important;
    color: white;
    font-size: 1.05rem;
  }
  
  .backToSearchBtn:active {
    color: white !important;
    font-size: 1rem;
  }
form{
    background: lightblue;
    border-radius: 15px;
}
.BookSearchLabel{
    margin-left:20px;
    margin-top: 10px
}
.submitBtn{
    margin-bottom: 10px;
    margin-left: 20px;
}
.form-control{
    margin-left: 20px;
    width: 95%
}
.card{
    border-radius: 10px;
    background-color: lightgrey
}

.search-list{
    border-radius: 15px;
    background-color: lightblue
}

.saveBook {
    border-radius: 5px;
    margin-left: 10px
}

.viewBook {
    border-radius: 5px;
    margin-left: 5px
}
.footer {
    height: 5vh;
    background-color: #8a4384;
    width: 100%;
    /* margin-top: 15vh; */
    /* margin-bottom: 0vh !important; */
}

.wrapper {
    width: 100%;
    display: flex;
    justify-content: center;
}

.builtBy {
    padding-top: 1.2vh;
    color: white;
}
.App {
    text-align: center;
  }
  
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 80px;
  }
  
  .App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
  }
  
  .App-intro {
    font-size: large;
  }
  
  @keyframes App-logo-spin {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
  }
